import platform from "../img/platform.png";
import hills from "../img/hills.png";
import background from "../img/background.png";
import platformSmallTall from "../img/platformSmallTall.png";
import audio from '../img/audio.mp3'
import star from '../img/star.png'
import { createImage, laserate } from "./utils";
import { Platform, Player, GenericObject, Star } from "./main";
console.log(audio)
let myAudio = new Audio(audio); 
if (typeof myAudio.loop == 'boolean')
{
    myAudio.loop = true;
}
else
{
    myAudio.addEventListener('ended', function() {
        this.currentTime = 0;
        this.play();
    }, false);
}
['keydown','click'].forEach(e=>{
    document.addEventListener(e,()=>{
        myAudio.play();
    })
    
})

console.log(platform);
const canvas = document.querySelector("canvas");

const c = canvas.getContext("2d");

canvas.width = 1024;
canvas.height = 570;



let platformImage = null;
let platformSmallTallImage = null;
let starImage = null;
let player = null;
let platforms = [];
let genericObjects = [];
let stars = [];
let lastKey;

const keys = {
  right: {
    pressed: false,
  },
  left: {
    pressed: false,
  },
};

let scrollOffset = 0;

function init() {
  starImage = createImage(star);  
  platformImage = createImage(platform);
  platformSmallTallImage = createImage(platformSmallTall);
  stars = [new Star(c,canvas, 50,350, starImage),new Star(c,canvas, 150,350, starImage),new Star(c,canvas, 250,350, starImage),new Star(c,canvas, 350,350, starImage)]
  player = new Player(c,canvas);
  platforms = [
    new Platform({
      x:
        platformImage.width * 4 +
        300 -
        2 +
        platformImage.width -
        platformSmallTallImage.width,
      y: 270,
      image: createImage(platformSmallTall),
      c,
    }),

    new Platform({ x: -1, y: 470, image: platformImage ,c}),
    new Platform({
      x: platformImage.width - 3,
      y: 470,
      image: platformImage,
      c,
    }),

    new Platform({
      x: platformImage.width * 2 + 100,
      y: 470,
      image: platformImage,
      c,
    }),

    new Platform({
      x: platformImage.width * 3 + 300,
      y: 470,
      image: platformImage,
      c,
    }),

    new Platform({
      x: platformImage.width * 4 + 300 - 2,
      y: 470,
      image: platformImage,
      c,
    }),

    new Platform({
      x: platformImage.width * 5 + 700 - 2,
      y: 470,
      image: platformImage,
      c,
    }),
  ];

  genericObjects = [
    new GenericObject({
      x: -1,
      y: -1,
      image: createImage(background),
      c,
    }),
    new GenericObject({
      x: -1,
      y: -1,
      image: createImage(hills),
      c,
    }),
  ];

   scrollOffset = 0;
}

function animate() {
    console.log( platformImage.width * 4 +
        300 -
        2 +
        platformImage.width -
        platformSmallTallImage.width);
  requestAnimationFrame(animate);
  c.fillStyle = "white";
  c.fillRect(0, 0, canvas.width, canvas.height);

  genericObjects.forEach((genericObject) => {
    genericObject.draw();
  });
  stars.forEach(star=>{
      if(star.visible){
          star.draw();
      }
  })
  platforms.forEach((platform) => {
    platform.draw();
  });
  player.update();

  if (keys.right.pressed && player.position.x < 400) {
    player.velocity.x = player.speed;
  } else if (
    (keys.left.pressed && player.position.x > 100) ||
    (keys.left.pressed && scrollOffset === 0 && player.position.x > 0)
  ) {
    player.velocity.x = -player.speed;
  } else {
    player.velocity.x = 0;

    if (keys.right.pressed) {
      scrollOffset += player.speed;
      stars.forEach(star=>{
          star.position.x -= player.speed;
      })
      platforms.forEach((platform) => {
        platform.position.x -= player.speed;
      });
      genericObjects.forEach((genericObject) => {
        genericObject.position.x -= player.speed * 0.66;
      });
    } else if (keys.left.pressed && scrollOffset > 0) {
      scrollOffset -= player.speed;
      stars.forEach(star=>{
        star.position.x += player.speed;
    })
      platforms.forEach((platform) => {
        platform.position.x += player.speed;
      });

      genericObjects.forEach((genericObject) => {
        genericObject.position.x += player.speed * 0.66;
      });
    }
  }

  //star collisoin detection
  stars.forEach(star=>{
    if(star.visible && player.position.y + player.height <= star.position.y &&
    player.position.y + player.height + player.velocity.y >=
      star.position.y &&
    player.position.x + player.width >= star.position.x &&
    player.position.x <= star.position.x + star.width ){
        star.visible = false;
        player.score+=1;
        laserate(220);
    }
  })
  // platform collision detection
  platforms.forEach((platform) => {
    if (
      player.position.y + player.height <= platform.position.y &&
      player.position.y + player.height + player.velocity.y >=
        platform.position.y &&
      player.position.x + player.width >= platform.position.x &&
      player.position.x <= platform.position.x + platform.width
    ) {
      player.velocity.y = 0;
    }
  });
  // sprite switching
  if (
    keys.right.pressed &&
    lastKey === "right" &&
    player.currentSprite !== player.sprites.run.right
  ) {
    player.frames = 1;
    player.currentSprite = player.sprites.run.right;
    player.currentCropWidth = player.sprites.run.cropWidth;
    player.width = player.sprites.run.width;
  } else if (
    keys.left.pressed &&
    lastKey === "left" &&
    player.currentSprite !== player.sprites.run.left
  ) {
    player.currentSprite = player.sprites.run.left;
    player.currentCropWidth = player.sprites.run.cropWidth;
    player.width = player.sprites.run.width;
  } else if (
    !keys.left.pressed &&
    lastKey === "left" &&
    player.currentSprite !== player.sprites.stand.left
  ) {
    player.currentSprite = player.sprites.stand.left;
    player.currentCropWidth = player.sprites.stand.cropWidth;
    player.width = player.sprites.stand.width;
  } else if (
    !keys.right.pressed &&
    lastKey === "right" &&
    player.currentSprite !== player.sprites.stand.right
  ) {
    player.currentSprite = player.sprites.stand.right;
    player.currentCropWidth = player.sprites.stand.cropWidth;
    player.width = player.sprites.stand.width;
  }

  // win condition
  if (scrollOffset > 2000) {
    console.log("You win");
  }

  // lose condition
  if (player.position.y > canvas.height) {
    init();
  }
  c.fillText(`score: ${player.score}`, 10, 50);
}
init();
animate();

addEventListener("keydown", ({ keyCode }) => {
  // console.log(keyCode)
  switch (keyCode) {
    case 65:
      console.log("left");
      keys.left.pressed = true;
      lastKey = "left";
      break;

    case 83:
      console.log("down");
      break;

    case 68:
      console.log("right");
      keys.right.pressed = true;
      lastKey = "right";
      break;
    case 87:
      console.log("up");
      if(player.velocity.y==0)
      player.velocity.y -= 30;
      break;
  }

  console.log(keys.right.pressed);
});

addEventListener("keyup", ({ keyCode }) => {
  // console.log(keyCode)
  switch (keyCode) {
    case 65:
      console.log("left");
      keys.left.pressed = false;
      break;

    case 83:
      console.log("down");
      break;

    case 68:
      console.log("right");
      keys.right.pressed = false;

      break;
    case 87:
      console.log("up");

      break;
  }

  console.log(keys.right.pressed);
});
