function randomIntFromRange(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

function randomColor(colors) {
  return colors[Math.floor(Math.random() * colors.length)]
}

function distance(x1, y1, x2, y2) {
  const xDist = x2 - x1
  const yDist = y2 - y1

  return Math.sqrt(Math.pow(xDist, 2) + Math.pow(yDist, 2))
}

function createImage(imageSrc) {
  const image = new Image()
  image.src = imageSrc
  return image
}

let audioContext = new AudioContext();
function laserate(a) {
  // create an oscillator node
  let osc = audioContext.createOscillator();
  // set type - can be sine, square, sawtooth, triangle
  osc.type = "triangle";
  // set frequency
  osc.frequency.value = a ? 192 * 5 : 5 * 150;
  // up frequency over a second
  osc.frequency.exponentialRampToValueAtTime(
      600,
      audioContext.currentTime + 1
  );

  // create gain node
  let gain = audioContext.createGain();

  // gain by default is 1 - drop it to near mute after about a second
  gain.gain.exponentialRampToValueAtTime(
      0.001,
      audioContext.currentTime +0.3
  );

  // start oscillator
  osc.start();
  // stop it after a second
  osc.stop(audioContext.currentTime + 1);
  // connect our graph
  osc.connect(gain).connect(audioContext.destination);
}

module.exports = { randomIntFromRange, randomColor, distance,createImage, laserate }
