import spriteRunLeft from '../img/spriteRunLeft.png'
import spriteRunRight from '../img/spriteRunRight.png'
import spriteStandLeft from '../img/spriteStandLeft.png'
import spriteStandRight from '../img/spriteStandRight.png'
import {createImage} from './utils'
const gravity = 1.5;
class Star{
    constructor(c,canvas, x, y, img){
        this.c = c;
        this.canvas = canvas;
        this.visible = true;
        this.position = {
            x: x,
            y: y
        }
        this.height = img.height;
        this.width = img.width;
        this.img = img

    }
    draw() {
        this.c.drawImage(
            this.img, 
            0,0,this.width, this.height,this.position.x,
            this.position.y, this.width, this.height)
    }
}
class Player {
    constructor(c,canvas) {
    this.canvas = canvas;
      this.speed = 10
        this.position = {
            x: 100,
            y: 100
        }
        this.velocity = {
            x: 0,
            y: 0
        }
        this.c = c;
        this.width = 66;
        this.height = 150;
        this.score = 0;
        this.image = createImage(spriteStandRight)
        this.frames = 0;
        this.sprites = {
            stand: {
                right: createImage(spriteStandRight),
                left: createImage(spriteStandLeft), 
                cropWidth: 177,
                width: 66
            },
            run: {
                right: createImage(spriteRunRight),
                left: createImage(spriteRunLeft),
                cropWidth: 341,
                width: 127.875
            }
        }
        
        this.currentSprite = this.sprites.stand.right
        this.currentCropWidth = 177
    }
    draw() {
        this.c.drawImage(
            this.currentSprite, 
            this.currentCropWidth * this.frames, 
            0,
            this.currentCropWidth,
            400,
            this.position.x,
            this.position.y, this.width, this.height)
    }

    update() {
        this.frames ++
        if (this.frames > 59 && (this.currentSprite ===
            this.sprites.stand.right || this.currentSprite ===
            this.sprites.stand.left))
        this.frames = 0
        else if (this.frames > 29 && (this.currentSprite ===
            this.sprites.run.right || this.currentSprite === 
            this.sprites.run.left))
            this.frames = 0
        this.draw()
        this.position.x += this.velocity.x
        this.position.y += this.velocity.y
        

        if (this.position.y + this.height +
            this.velocity.y <= this.canvas.height)
        this.velocity.y += gravity
        
    }

}

class Platform {
    constructor({x, y, image,c}) {
        this.position = {
            x,
            y
        }
        this.c = c;
        this.image = image
        this.width = image.width
        this.height = image.height

        
    }

    draw() {
        this.c.drawImage(this.image, this.position.x,
          this.position.y)
    }
}

class GenericObject {
  constructor({x, y, image,c}) {
      this.position = {
          x,
          y
      }
      console.log(c);
      this.c = c;
      
      this.image = image
      this.width = image.width
      this.height = image.height

      
  }

  draw() {
      this.c.drawImage(this.image, this.position.x,
        this.position.y)
  }
}

export {Platform, Player,GenericObject, Star};